* {
  outline: none;
}

body {
  margin: 0;
  overflow-y: overlay;
  font-family: "Poppins Regular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}

body ::-webkit-scrollbar-thumb {
  background-color: hsla(0, 0%, 100%, 0.16);
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}

::-webkit-scrollbar-track {
  background: hsla(0, 0%, 100%, 0.1);
}

.btn {
  border-radius: 50px;
  padding: 14px 0;
  width: 160px;
  cursor: pointer;
  transition: 0.2s;
  font-family: "Poppins SemiBold";
}

.btn:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.primary-btn {
  color: var(--white);
  border: 2px solid linen;
  font-size: 12px;
  background-color: #1f2235;
}
/* @media only screen and (max-width: 414px) {
  .primary-btn {
    font-size: 13px;
  }
} */

.primary-btn:hover {
  color: aliceblue;
  border: 2px solid #ff5823;
  background-color: #1f2235;
}

.highlighted-btn {
  color: var(--white);

  font-size: 12px;
  background-color: #ff5823;
}
@media only screen and (max-width: 568px) {
  .highlighted-btn {
    z-index: -900;
  }
}

.red-border {
  border: 1px solid var(--error-message) !important;
}

.fade-in {
  opacity: 0;
  -webkit-transform: translateY(80px);
          transform: translateY(80px);
  transition: all 0.8s ease;
}

:root {
  --light-blue: #04c3ff;
  --light-blue-shadow: rgb(69 211 255 / 27%);
  --sea-green: #0abead;
  --sea-green-dark: #0cbfae;
  --sea-green-extra-dark: #09bead;
  --sea-green-shadow: rgba(11, 190, 173, 0.4);
  --header-hamburger-background: rgba(11, 190, 173, 0.8);
  --orange: #f57f17;
  --dark-orange: #ff5823;
  --orange-shadow: rgba(245, 127, 23, 0.2);
  --white: #ffffff;
  --black: #333333;
  --disabled-btn: #09bead7a;
  --error-message: #e44f4fd1;
  --toast-error: #e44f4ffa;
  --toast-shadow: #9c9c9ce3;
  --toast-success: #0cbfae;
  --loader-color: #0cbfae;
}

.profile-container {
  /* display: flex; */
  align-items: center;
  text-align: center;
  justify-content: center;
  height: 100vh;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  background-image: url(/Portfolio/static/media/bg.01f450f6.jpg);
}

.profile-parent {
  /* display: flex; */
  align-items: center;
  color: aliceblue;
}

.profile-details-name {
  font-size: 24px;
  font-family: "Poppins SemiBold";
}

.profile-details-role {
  display: flex;
  flex-direction: column;
  margin: 14px 0 24px 0;
}

.profile-details-role h1 {
  font-size: 40px;
  font-family: "cursive";
}

.primary-text {
  color: (--white);
}

.styles_typicalWrapper__1_Uvh {
  font-size: 30px;
}

.highlighted-text {
  color: #ff5823;
}

.profile-role-tagline {
  font-size: 19px;
  margin: 5px 0 0 0;
  font-family: "Poppins Light";
}

.profile-options .highlighted-btn {
  margin: 0 0 0 28px;
  border-radius: 12px solid white;
}

.profile-options .highlighted-btn:hover {
  margin: 0 0 0 28px;
  background-color: cornsilk;
  color: #111;
}

.profile-picture {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 1px 0px 0px var(--white);
  border-radius: 50%;
  height: 380px;
  width: 380px;
  margin: 0 0px 0 100px;
}

.profile-picture-background {
  height: 92%;
  width: 92%;
  border-radius: 50%;
  background-image: url(/Portfolio/static/media/profilephoto.e40c03c5.JPG);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}

.profile-picture-background:hover {
  -webkit-transform: scale(1.07);
          transform: scale(1.07);
  transition: 1s ease-out;
}

.colz .colz-icon i {
  color: white;
  padding: 8px;
  padding-left: 0px;
}

.colz .cols-icon i:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.hidefooter {
  transition: ease-in-out 0.5s;
}

/*IPAD PRO RESPONSIVENESS*/
@media only screen and (max-width: 1110px) {
  .profile-picture {
    margin: 0 0px 0 80px;
  }

  .profile-details-name {
    font-size: 20px;
  }

  .profile-details-role .primary-text {
    font: 12px;
  }

  .profile-options .btn {
    width: 120px;
    padding: 11px 0;
    z-index: 700;
  }
}

@media only screen and (max-width: 910px) {
  .profile-picture {
    margin: 0 0 0 4px;
    height: 320px;
    width: 320px;
  }
}

@media only screen and (max-width: 810px) {
  .profile-picture {
    margin: auto !important;
  }

  .profile-parent {
    flex-direction: column-reverse;
    margin: 20px 0 0 0;
  }

  .profile-options {
    display: flex;
    justify-content: space-between;
  }

  .proflie-details {
    margin: 25px 0 0;
  }

  .profile-options .highlighted-btn {
    margin: 0 !important;
  }

  .profile-details-name,
  .profile-details-role {
    text-align: center;
  }
}

@media only screen and (min-width: 810px) {
  .icon-margin {
    margin: auto;
    margin-left: 25%;
  }
}

@media only screen and (max-width: 810px) {
  .icon-margin {
    margin: auto;
  }
}

@media only screen and (max-width: 375px) {
  .profile-picture {
    height: 275px;
    width: 275px;
    margin-top: -90px;
  }

  .proflie-details {
    width: 95%;
  }
}

.wave {
  display: none;
}

@media only screen and (min-width: 1000px) {
  .wave {
    display: block;
  }
}
.footer-container {
  height: 17.8%;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
}
.foorer-parent {
  width: 100%;
  height: 100%;
  max-width: 1920px;
}
.footer-parent img {
  max-width: 100%;
  height: 130px;
}
/*IPAD PRO RESPONSIVENESS*/
@media only screen and (max-width: 1110px) {
  .footer-parent {
    display: flex;
    align-items: flex-end;
  }
}

.wave-top {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 45%;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.home-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100%;
  background-image: url(/Portfolio/static/media/bg.01f450f6.jpg);
}

.timeline-Img {
  background-image: url(/Portfolio/static/media/bg.01f450f6.jpg);
}

.vertical-timeline-element-date{
  color: white !important;
  padding: 50px !important;
}

.vertical-timeline::before {
  height: 90% !important;
}

@media only screen and (min-width: 710px) {
  .skillposition {
    margin-left: 3rem;
  }
}

